export default {
	data() {
		return {
			isStart: false, //是否启动
		}
	},
	onLoad() {
		this.getCurStatus()
	},
	methods: {
		getCurStatus() {
			uni.showLoading({
				title: '数据加载中',
				mask: true
			})
			this.$api.ajax({
				url: this.$config.getStartInfo
			})
			.then((res) => {
				if(res.statusCode == 200 && res.data.code == 0) {
					this.isStart = res.data.data == 1?true: false
					setTimeout(() => {
						uni.hideLoading();
					}, 200)
				}else {
					uni.hideLoading();
					this.$api.warnNotice(res.data.message)
				}
			})
			.catch(() => {
				uni.hideLoading();
				this.$api.warnNotice('获取数据失败')
			})
		},
		toStartPlace() {
			const $this = this
			if(!this.isStart) {
				this.$api.ajax({
					url: this.$config.toClickStart,
					method: 'PUT'
				})
				.then((res) => {
					if(res.statusCode == 200 && res.data.code == 0) {
						uni.showToast({
						    title: '启动成功',
						    icon: 'success',
						    success: () => {
						      setTimeout(() => {
								$this.isStart = true
						      }, 1500)
						    }
						})
					}else {
						uni.hideLoading();
						this.$api.warnNotice(res.data.message)
					}
				})
				.catch(() => {
					uni.hideLoading();
					this.$api.warnNotice('操作失败')
				})
			}
		}
	}
}
